import styled from "styled-components";
import logoImage from "../logo.jpeg"; // Zmie ciek do swojego obrazu

export const Logo = styled.div`
  width: 100px;
  height: 100px;
  margin:auto;
  margin-top:40px;// Wyrodkowanie logo na osi poziomej
  border-radius:10%; // Zaokrglenie logo
  background-image: url(${logoImage});
  background-size: cover; // Dostosuj do potrzeb
  // Moesz rwnie doda inne waciwoci ta, takie jak repeat, position itp.
  transform: scale(1.5); // Powikszenie logo o 20%
  border:5px solid #064781;
  box-shadow: 1px 5px 10px  #186cb5 ;
`;
